











import Vue from 'vue'
import { mapState, mapMutations } from 'vuex'

declare const moment: any
declare const Chart: any

interface ChartData {
  close: number
  high: number
  low: number
  open: number
  time: string
}

interface ConvertedData {
  c: number
  h: number
  l: number
  o: number
  t: number
}

interface Data {
  ctx: CanvasRenderingContext2D | null
}

export default Vue.extend({
  name: 'Chart',

  data(): Data {
    return {
      ctx: null,
    }
  },

  computed: {
    ...mapState(['chartType', 'info']),
  },

  methods: {
    ...mapMutations(['setChartType']),
    convertDataStructure(data: ChartData[]): ConvertedData[] {
      return data.map((d) => {
        return {
          c: d.close,
          h: d.high,
          l: d.low,
          o: d.open,
          t: moment(d.time).valueOf() as number,
        }
      })
    },
    getChartData() {
      if (!this.info) { return }
      return this.chartType === 'hour' ? this.info.chart_by_hour
        : this.chartType === 'min' ? this.info.chart_by_min
        : this.chartType === 'sec' ? this.reduceChartData(this.info.chart_by_sec)
        : null
    },
    reduceChartData(chart: ChartData[]) {
      return chart.filter((data, index) => index < 60 * 3)
    },
    setupContext2d() {
      const canvas = this.$refs.canvas as HTMLCanvasElement
      this.ctx = canvas.getContext('2d')
      if (!this.ctx) { return }

      this.ctx.canvas.width = 900
      this.ctx.canvas.height = 400
    },
    showChart() {
      if (!this.info) { return }

      const candlestickChart = new Chart(
        this.ctx,
        {
          type: 'candlestick',
          data: {
            datasets: [{
              label: 'ISUCOIN Chart',
              data: this.convertDataStructure(this.getChartData()),
            }],
          },
        },
      )
    },
  },

  mounted() {
    this.setupContext2d()
    this.showChart()
  },

  watch: {
    chartType() {
      this.showChart()
    },
    info() {
      this.showChart()
    },
  },
})
